import React from 'react';
import pushupsContext from '../state/PushUpsContextState';
import { postJsonDataUrlAfterAPIpushups } from '../../../functions/postJsonData';



const Rescheduled = ({ usability, usabilityOld, days }) => {
  let { user, updateUser } = React.useContext(pushupsContext);
  let [stuffToUpdate, setStuffToUpdate] = React.useState(null);

  const [readAgain, setReadAgain] = React.useState(null);


  ///////reschedule
  React.useEffect(() => {
    if (user) {
      let updatedSchedule = user.pushupPlans.current.schedule;
      let scheduleDayNumberFrom0 = user.pushupPlans.current.scheduleDayToDoFrom0;
      // let scheduleDayNumberFrom0 = 4;
      // console.log("scheduleDayNumberFrom0: ", scheduleDayNumberFrom0);
      //populate schedule starting with current day with new dates
      let timeNowInMs = new Date().getTime();
      let counter = 0;
      for (let i = scheduleDayNumberFrom0; i < updatedSchedule.length; i++) {
        updatedSchedule[i].dueDate = new Date(timeNowInMs + 24 * 60 * 60 * 1000 * counter); //i adds 1 day extra starting with nothing for today
        counter++;
      }
      // console.log("updated schedule: ", currentScheduleToUpdate);
     
      setStuffToUpdate({ usability, updatedSchedule });//needs to be in the ReactEffect or sth, otherwise it would keep re-rendering
    }
  }, [user]);




  const continueToTraining = async () => {
    console.log("continue");

    const updatedUser = await postJsonDataUrlAfterAPIpushups('/reschedule', stuffToUpdate); //and final as other option
    // console.log("updatedUser-rescheduled", updatedUser);
    updateUser({...updatedUser});
    //update user on DB then here

    //load training



  }


  return (
    <div>
      {/* usability */}
      <h1>Updating data "usability"...</h1>
      <p>You are {days} day(s) late with your training.</p>
      <p>Every time you miss or don't finish a training, the ability to measure how effective this push-up plan is goes down. </p>

      <p>Until now effect of the pushup plan on your improvement was possible to measure with
      <span style={{ fontSize: '2rem' }}>{usabilityOld.rating}%</span> . </p>
      <p>Due to late start, usability rating will be changed to <span style={{ fontSize: '2rem' }}>{usability.rating}%</span> .</p>


      <p>Once you are under 50%, you may consider committing to a new plan.</p>
      <p>If you can stick to the plan schedule and get 100%, that would be impressive!</p>


      {/* schedule */}
      <h1>Updating schedule</h1>
      <p>The training you missed will be rescheduled for today and started now. Future trainings will be rescheduled accordingly.</p>

      <button onClick={continueToTraining}>Understood</button>
      <button onClick={() => setReadAgain(true)}>Not Understood</button>
      {readAgain === null ? null : <p>Read again.</p>}
    </div>
  );
};

export default Rescheduled;