// import React from 'react';
import React from 'react';
import pushupsContext from './state/PushUpsContextState';
import { returnDaysDifference } from './functions/basics';


import { Link } from 'react-router-dom';

import styled from 'styled-components'

export const SLink = styled(Link)`
display: block;
font-size:2rem;
margin:10px;
background-color:orange;
padding: 3px;
border-radius:3px;
text-decoration:none;
text-align: center;
&:hover{ 
  background-color:#996b15;
  color:white;
  text-decoration:none;
  /* font-size:20px; */
}
/* transition: background-color, color; */
transition-duration: 0.25s;
`
export const returnNumberOfDaysMissingCurrentSchedulleOrUndefined = (user) => {
  if (user.pushupPlans.current === null || user.pushupPlans.current === undefined) return undefined;
  let currentPlan = user.pushupPlans.current;
  let dayToDoFrom0 = currentPlan.scheduleDayToDoFrom0;
  if (dayToDoFrom0 === undefined || dayToDoFrom0 === null) return undefined;

  // there is current plan with scheduled day; carry on...
  let trainingThisMAnyDaysAhead = returnDaysDifference(new Date(), currentPlan.schedule[dayToDoFrom0].dueDate);
  // console.log("trainingThisMAnyDaysAhead: ", trainingThisMAnyDaysAhead);
  return trainingThisMAnyDaysAhead;
}

const Home = (props) => {
  const { user, updateUser } = React.useContext(pushupsContext);
  const [info, setInfo] = React.useState("loading Data... (Pray everyone:-)");
  let dueDateIsAheadThisMuch;

  //check if today's date matches the date of the training to do

  React.useEffect(() => {
    if (user) {
      dueDateIsAheadThisMuch = returnNumberOfDaysMissingCurrentSchedulleOrUndefined(user);
      // console.log("dueDateIsAheadThisMuch: ", dueDateIsAheadThisMuch);
      let textInfoLocalVariable;
      // switch (dueDateIsAheadThisMuch) {
      // case null:
      //   textInfoLocalVariable = 'loading Data... (Pray everyone:-))';
      //   break;

      textInfoLocalVariable =
        dueDateIsAheadThisMuch === undefined ? 'no scheduled date'
          : dueDateIsAheadThisMuch === 0 ? 'Your training is Due'
            // : dueDateIsAheadThisMuch === 1 ? 'Your training is Due tomorrow'
            : dueDateIsAheadThisMuch === 1 ? 'tomorrow'
              // : dueDateIsAheadThisMuch > 1 ? `Your Training is Due in ${dueDateIsAheadThisMuch} days.`
              : dueDateIsAheadThisMuch > 1 ? `in ${dueDateIsAheadThisMuch} days`
                : dueDateIsAheadThisMuch === -1 ? `You are 1 day late.`
                  : dueDateIsAheadThisMuch < -1 ? `You are ${-dueDateIsAheadThisMuch} days late.`
                    : '-...-';

      // console.log("textInfoLocalVariable: ", textInfoLocalVariable);
      setInfo({text:textInfoLocalVariable, days:dueDateIsAheadThisMuch});
      // setTextInfo("Ahoj");
    }
  }, [user]);


  // console.log("textInfo: ", textInfo);
  return (
    <div>
      <h1>Push-ups - logo,img,styled intro screen...</h1>
      <SLink to='/pushups/maxRepsTest'>Test Your Max Reps</SLink>
      <SLink to='/pushups/trainingPlans'>Training Plans</SLink>

      {info.days > 0 ?
        <SLink to='/pushups' style={{ backgroundColor: 'gray',fontSize:'1rem',color:'lightBlue',cursor:'not-allowed' }}>
          Start Training
          <p style={{ fontSize: '2rem', color: 'white' }}>{info.text}</p>
          </SLink>
        : <SLink to='/pushups/startMyTraining'>
          Start Training
        <p style={{ fontSize: '1rem', color: 'gray' }}>{info.text}</p>
        </SLink>
      }

      {/*if training was sucessfully finished, label it  */}
      {/* check if day of last training (if any exists) is today. if yes and training not finished, show "continue training option" */}



      <br /><br />
      {/* show only if user exists: */}<SLink to='/pushups/history'>History</SLink>
      {/* show only if user exists: */}<SLink to='/pushups/userProfile'>My profile / Settings)</SLink>
    </div>
  );
};

export default Home;